import React from "react";
import { motion } from "framer-motion";
import Section from "../scripts/Section";
import CustomMarquee from "../scripts/CustomMarquee";
import Text from "../scripts/Text";
import RevealText from "../scripts/RevealText";

export function About({ bgGridCross2, sections, bgX, bgY }) {
  return (
    <div className="overflow-hidden relative bg-black">
      <motion.div
        className="absolute inset-0 bg-[length:512] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-20"
        style={{
          backgroundImage: `url(${bgGridCross2})`,
          mixBlendMode: "normal",
          backgroundPositionX: bgX,
          backgroundPositionY: bgY,
        }}
      />

      <Section id="about" className="relative">
        <CustomMarquee
          name={sections.at(1)}
          direction={
            sections.indexOf(sections.at(1)) % 2 === 0 ? "right" : "left"
          }
          className="relative z-20"
        />
        <div className="flex justify-center text-white z-10 py-16 lg:py-32 px-8">
          <div className="flex flex-row justify-self-center py-2 gap-x-6 lg:gap-x-12 max-w-6xl grow">
            <div className="flex flex-col gap-y-6 lg:gap-y-12">
              <div className="hidden lg:block text-offWhite text-5xl lg:text-8xl font-justSansBold font-bold tracking-tighter">
                {Text.about.subtitle}
              </div>
              <div className="text-base lg:text-lg font-playfair text-justify tracking-widest">
                {Object.keys(Text.about)
                  .filter((key) => key.startsWith("section"))
                  .map((key, index) => (
                    <React.Fragment key={key}>
                      <RevealText
                        delay={300 * (index + 1)}
                        duration={1000}
                        className="indent-4"
                      >
                        {Text.about[key]}
                      </RevealText>
                      <br />
                    </React.Fragment>
                  ))}
              </div>
            </div>
            <div
              className="hidden lg:block z-10 text-transparent text-6xl lg:text-9xl font-justSansBold font-bold tracking-tighter [writing-mode:vertical-lr]"
              style={{
                WebkitTextStroke: "2px var(--red-900)",
              }}
            >
              {Text.about.title}
            </div>
          </div>
        </div>
      </Section>
    </div>
  );
}
