import dacia1300 from "../assets/png/cars/dacia1300.png";
import ferrariF40 from "../assets/png/cars/ferrarif40.png";
import gtrR32 from "../assets/png/cars/gtrr32.png";
import peugeot205 from "../assets/png/cars/peugeot205.png";
import peugeot206 from "../assets/png/cars/peugeot206.png";
import trueno from "../assets/png/cars/trueno.png";

import { Swiper, SwiperSlide } from 'swiper/react';
import React from 'react';
import 'swiper/css';

import "swiper/swiper-bundle.css";

import { Autoplay, EffectCoverflow, Pagination } from 'swiper/modules';

const Carousel = ({ className = '' }) => {
    const swiperRef = React.useRef(null);

    const carImages = [
        { src: gtrR32, alt: "", className: "bg-black" },
        { src: dacia1300, alt: "", className: "bg-black" },
        { src: ferrariF40, alt: "", className: "bg-black" },
        { src: peugeot205, alt: "", className: "bg-white" },
        { src: peugeot206, alt: "", className: "bg-black" },
        { src: trueno, alt: "", className: "bg-black" }
    ];

    return (      
        <div className={`${className}`}>
            <Swiper
                effect={'coverflow'}
                autoHeight={false}
                centeredSlides={true}
                breakpoints={{
                    320: { // For mobile screens
                      slidesPerView: 1,
                      spaceBetween: 0,
                    },
                    768: { // For tablets and larger screens (desktop)
                      slidesPerView: 2,
                      spaceBetween: 16,
                    },
                }}
                loop={true}
                speed={600}
                grabCursor={true}
                initialSlide={0}
                autoplay={{
                    delay: 3000,
                    disableOnInteraction: false,
                }}
                coverflowEffect={{
                    rotate: 60,
                    stretch: -256,
                    depth: 600,
                    modifier: 1,
                    slideShadows: false,
                }}
                pagination={{
                    el: ".custom-pagination",
                    dynamicBullets: false,
                    type: 'bullets',
                }}
                onSwiper={(swiper) => {
                    swiperRef.current = swiper;
                }}
                onClick={() => {
                    if (swiperRef.current) {
                        if (swiperRef.current.clickedIndex > swiperRef.current.activeIndex){
                            swiperRef.current.slideNext();
                        }
                        else if (swiperRef.current.clickedIndex < swiperRef.current.activeIndex){
                            swiperRef.current.slidePrev();
                        }
                        else {
                            return;
                        }
                    }
                }}
                modules={[Autoplay, EffectCoverflow, Pagination]}
                className="mySwiper"
            >
                {carImages.map((car, index) => (
                    <SwiperSlide key={index}>
                        <img src={car.src} alt={car.alt} className={`${car.className} p-8`} />
                    </SwiperSlide>
                ))}
            </Swiper>
            <div className="custom-pagination mt-12 flex justify-center"></div>
        </div>   
    )
};
export default Carousel;