import React, { useEffect, useState } from "react";
import { motion, useInView } from "framer-motion";

const RevealText = ({
  children,
  delay = 0,
  duration = 1000,
  senseScroll = true,
  className = "",
}) => {
  const [isVisible, setIsVisible] = useState(false);
  const [showText, setShowText] = useState(false);
  const ref = React.useRef(null);
  const isInView = useInView(ref, { once: false });

  useEffect(() => {
    if (senseScroll) {
      if (isInView) {
        return triggerTextReveal(setIsVisible, setShowText, duration, delay);
      } else {
        setIsVisible(false);
        setShowText(false);
      }
    } else {
      return triggerTextReveal(setIsVisible, setShowText, duration, delay);
    }
  }, [isInView, duration, delay, senseScroll]);

  const animationStyles = {
    "--delay": `${delay}ms`,
    "--duration": `${duration}ms`,
  };

  return (
    <motion.p
      className={"reveal-text " + className}
      style={animationStyles}
      ref={ref}
    >
      <span
        className="reveal-text-after"
        style={{
          animation: isVisible
            ? `text-revealer var(--duration) cubic-bezier(0.0, 0.0, 0.2, 1) forwards`
            : "none",
          animationDelay: isVisible ? "var(--delay)" : "0s",
        }}
      />
      <span style={{ opacity: showText ? 1 : 0, transition: `opacity 0ms`}}>
        {children}
      </span>
      <style jsx>{`
        .reveal-text {
          position: relative;
          overflow: hidden;
          display: inline-block;
        }

        .reveal-text-after {
          content: "";
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;

          background-color: var(--red-900);
          transform: scaleX(0);
          transform-origin: 0 50%;
          pointer-events: none;
        }

        @keyframes text-revealer {
          0%,
          50% {
            transform-origin: 0 50%;
          }

          60%,
          100% {
            transform-origin: 100% 50%;
          }

          60% {
            transform: scaleX(1);
          }

          100% {
            transform: scaleX(0);
          }
        }
      `}</style>
    </motion.p>
  );
};

function triggerTextReveal(setIsVisible, setShowText, duration, delay) {
  setIsVisible(true);
  const timeout = setTimeout(() => {
    setShowText(true);
  }, duration / 2 + delay + 100);
  return () => clearTimeout(timeout);
}

export default RevealText;
