import Marquee from "react-fast-marquee";
import Star4Sides from "./Star4Sides";
import React from "react";
import grunge from "../assets/png/grungy.png";

const CustomMarquee = ({
  name = "marquee",
  direction = "left",
  className = "",
}) => {
  return (
    <div
      className={`flex justify-center bg-red-900 mix-blend-screen ${className}`}
    >
      <Marquee speed={60} autoFill={true} direction={direction}>
        <div
          className="z-[-1] absolute inset-0 bg-[length:128px] bg-repeat bg-fixed opacity-20"
          style={{
            backgroundImage: `url(${grunge})`,
            mixBlendMode: "normal",
          }}
        />
        <div className="py-4 flex items-center justify-between grow mr-12 lg:mr-24 gap-12 lg:gap-24 overflow-hidden">
          <div className="text-offWhite text-6xl lg:text-9xl font-justSansBold font-bold tracking-tighter text-end">
            {name.toUpperCase()}
          </div>
          <div className="pr-2 text-black text-6xl lg:text-9xl font-justSansBold font-bold tracking-tighter text-end">
            {"///"}
          </div>
          <div className="flex flex-row space-x-2 lg:space-x-4 items-center justify-center">
            <Star4Sides
              custom={1}
              className="h-10 lg:h-24 stroke-2 stroke-offWhite fill-transparent"
            />
            <Star4Sides
              custom={2}
              className="h-10 lg:h-24 stroke-2 stroke-offWhite fill-offWhite"
            />
          </div>
          <div className="pr-2 text-black text-6xl lg:text-9xl font-justSansBold font-bold tracking-tighter text-end">
            {"///"}
          </div>
        </div>
      </Marquee>
    </div>
  );
};
export default CustomMarquee;
