import React from "react";
import { motion } from "framer-motion";

const draw = {
  hidden: { pathLength: 0, opacity: 0 },
  visible: (i) => {
    const delay = (i - 1) * 0.3;
    return {
      pathLength: 1,
      opacity: 1,
      transition: {
        pathLength: { delay, type: "spring", duration: 2, bounce: 0 },
        opacity: { delay, duration: 1 }
      }
    };
  }
};

const Star4Sides = ({ className = "", custom = "", animated = false }) => {
  return (
    <motion.svg
      className={`${className}`}
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 64 64"
      initial={animated ? "hidden" : false}
      animate={animated ? "visible" : false}
    >
      <motion.path variants={draw} custom={custom} d="M63.898,31.688c-27.407-0.126-31.791,4.257-31.753,31.754C32.108,35.944,27.8,31.636,0.392,31.688
      C27.8,31.636,32.183,27.253,32.146-0.065C32.183,27.253,36.491,31.562,63.898,31.688z"/>
    </motion.svg>
  );
};

export default Star4Sides;