import React from "react";
import { motion } from "framer-motion";
import Section from "../scripts/Section";
import Star4Sides from "../scripts/Star4Sides";
import Text from "../scripts/Text";
import RevealText from "../scripts/RevealText";

export function Hero({
  bgTileableStripe,
  grunge,
  bgGrid,
  scrollY,
  showMotto,
  bgStripeX,
  bgStripeY,
  bgX,
  bgY,
  useWindowSize,
}) {
  return (
    <Section id="hero" className="relative">
      <motion.div
        className="absolute inset-0 bg-[length:512] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-100"
        style={{
          backgroundImage: `url(${bgGrid})`,
          mixBlendMode: "soft-light",
          backgroundPositionX: bgX,
          backgroundPositionY: bgY,
        }}
      />
      <motion.div
        className="absolute inset-0 bg-[length:512] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-100"
        style={{
          backgroundImage: `url(${bgTileableStripe})`,
          mixBlendMode: "multiply",
          backgroundPositionX: bgStripeX,
          backgroundPositionY: bgStripeY,
        }}
      />

      <div
        className="absolute inset-0 bg-[length:128px] bg-repeat bg-fixed opacity-100"
        style={{
          backgroundImage: `url(${grunge})`,
          mixBlendMode: "multiply",
        }}
      />

      <div className=" absolute inset-0 w-full h-full bg-gradient-to-t from-[rgba(0,0,0,0.5)] from-0% to-transparent to-100%" />

      {/* Motto */}
      <motion.div
        className="absolute top-12 lg:top-0 left-0 m-8 lg:m-12 text-2xl lg:text-6xl text-offWhite [writing-mode:vertical-lr]"
        initial={{
          y: 0,
        }}
        animate={{
          y: Math.min(scrollY, useWindowSize().h),
        }}
        transition={{
          ease: "circOut",
          duration: 0.5,
        }}
      >
        <motion.p
          initial={{
            opacity: 0,
            x: -100,
          }}
          animate={
            showMotto
              ? {
                  opacity: 0,
                  x: -100,
                }
              : {
                  opacity: 1,
                  x: 0,
                }
          }
          transition={
            showMotto
              ? {
                  type: "spring",
                  stiffness: 100,
                }
              : {
                  delay: 0,
                  type: "spring",
                  stiffness: 100,
                }
          }
        >
          <RevealText
            className="font-mishimishi text-white p-2"
            delay={1000}
            duration={1000}
          >
            {Text.hero.motto}
          </RevealText>
        </motion.p>
      </motion.div>

      <motion.div
        animate={{
          y: Math.min(scrollY, useWindowSize().h / 1.9),
          scale: Math.max(1 - scrollY / 1000, 0.75),
        }}
        transition={{
          ease: "circOut",
          duration: 0.5,
        }}
        style={{
          transformOrigin: "center top",
        }}
      >
        <div className="relative h-svh container mx-auto px-6 lg:px-8 py-24 lg:py-32 flex items-center justify-center">
          <div className="center z-1 space-y-8">
            <div className="w-full flex flex-row space-x-2 lg:space-x-4 items-center justify-center">
              <motion.div
                initial={{
                  y: -100,
                }}
                animate={{
                  y: 0,
                }}
                transition={{
                  type: "spring",
                  stiffness: 100,
                }}
              >
                <Star4Sides
                  custom={1}
                  className="h-10 lg:h-24 stroke-2 stroke-offWhite fill-transparent"
                  animated={true}
                />
              </motion.div>
              <motion.div
                initial={{
                  y: 100,
                }}
                animate={{
                  y: 0,
                }}
                transition={{
                  delay: 0.3,
                  type: "spring",
                  stiffness: 100,
                }}
              >
                <Star4Sides
                  custom={2}
                  className="h-10 lg:h-24 stroke-2 stroke-offWhite fill-offWhite"
                  animated={true}
                />
              </motion.div>
              <motion.h1
                className="text-offWhite text-5xl md:text-6xl lg:text-9xl font-justSansBold font-bold tracking-tighter"
                initial={{
                  opacity: 0,
                  y: -100,
                }}
                animate={{
                  opacity: 1,
                  y: 0,
                }}
                transition={{
                  delay: 0.6,
                  duration: 1,
                  type: "spring",
                  stiffness: 100,
                }}
              >
                {Text.hero.title}
              </motion.h1>
            </div>
            <motion.p
              className="z-[-1] bg-red-900 font-playfair text-sm lg:text-2xl tracking-widest text-center"
              initial={{
                opacity: 0,
              }}
              animate={{
                opacity: 1,
              }}
              transition={{
                delay: 1,
                duration: 1,
              }}
            >
              <RevealText delay={1000} duration={1000} senseScroll={false}>
                {Text.hero.characteristics}
              </RevealText>
            </motion.p>
          </div>
        </div>
      </motion.div>
    </Section>
  );
}
