import React from "react";
import { motion } from "framer-motion";
import Section from "../scripts/Section";
import CustomMarquee from "../scripts/CustomMarquee";
import { useState, useEffect, useRef } from "react";

const News = ({ bgGridCross2, bgGrid2, bgX, bgY, sections, headerHeight }) => {
  const [openArticles, setOpenArticles] = useState([]);
  const [articles, setArticles] = useState([]);
  const articleRefs = useRef([]);
  const [lastIndex, setLastIndex] = useState(null);

  const toggleArticle = (index) => {
    setOpenArticles((prevOpen) => {
      if (prevOpen.includes(index)) {
        setLastIndex(null);
        return [];
      } else {
        return [index];
      }
    });
  };

  const handleAnimationComplete = (index) => {
    if (lastIndex != null && lastIndex < index) {
      const elementPosition =
        articleRefs.current[index].getBoundingClientRect().top + window.scrollY;
      const offsetPosition = elementPosition - headerHeight;
      window.scrollTo({
        top: offsetPosition,
        behavior: "smooth",
      });
    }
    setLastIndex(index);
  };

  useEffect(() => {
    const fetchArticles = () => {
      const articleFiles = require.context("./news", false, /\.js$/);
      const importedArticles = articleFiles
        .keys()
        .map((file) => articleFiles(file).default);

      setArticles(importedArticles);
    };

    fetchArticles();
  }, []);

  return (
    <div className="overflow-hidden relative bg-black">
      <motion.div
        className="absolute inset-0 bg-[length:768px] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-20"
        style={{
          backgroundImage: `url(${bgGridCross2}), url(${bgGrid2})`,
          backgroundPositionX: bgX,
          backgroundPositionY: bgY,
        }}
      />
      <Section id="news" className="relative">
        <CustomMarquee
          name={sections.at(3)}
          direction={
            sections.indexOf(sections.at(3)) % 2 === 0 ? "right" : "left"
          }
        />

        <div className="flex justify-center text-white z-10 py-16 lg:py-32 px-8">
          <div className="flex flex-row justify-self-center py-2 gap-x-6 lg:gap-x-12 max-w-6xl grow">
            <div className="w-full flex flex-col gap-y-4">
              {articles.map((article, index) => {
                const isOpen = openArticles.includes(index);

                return (
                  <div
                    key={index}
                    className="relative"
                    ref={(el) => (articleRefs.current[index] = el)}
                  >
                    <div
                      className="relative bg-darkRotaryGreen flex gap-3 lg:gap-6 pr-3 lg:pr-6 items-center"
                      onClick={() => toggleArticle(index)}
                    >
                      <img
                        src={article.image}
                        alt=""
                        className="z-10 w-32 lg:w-48 h-auto"
                      />
                      <div className="flex flex-row justify-between items-center grow p-3 lg:p-6">
                        <div className="flex flex-col justify-center">
                          <h3 className="text-offWhite text-xl lg:text-2xl font-justSansBold">
                            <p className="hidden lg:inline-block">
                              {"///"}&ensp;
                            </p>
                            {article.title}
                          </h3>
                          <p className="text-sm font-justSans">
                            {article.date}
                          </p>
                        </div>
                        <PlusMinusToggle isOpen={isOpen} />
                      </div>
                      <div className="z-10 absolute inset-0 cursor-pointer lg:hover:bg-red-900 duration-300 mix-blend-exclusion" />
                    </div>

                    <motion.div
                      className="relative border-l-4 border-r-4 border-b-4 lg:border-l-8 lg:border-r-8 lg:border-b-8 border-dashed border-darkRotaryGreen bg-black"
                      initial={{ height: 0, opacity: 0 }}
                      animate={{
                        height: isOpen ? "auto" : 0,
                        opacity: isOpen ? 1 : 0,
                      }}
                      transition={{ ease: "circInOut", duration: 0.3 }}
                      onAnimationComplete={() =>
                        isOpen && handleAnimationComplete(index)
                      }
                      style={{ overflow: isOpen ? "visible" : "hidden" }}
                    >
                      <div className="p-4 lg:p-12 text-base lg:text-lg text-justify tracking-wider">
                        {article.content}
                      </div>
                    </motion.div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </Section>
    </div>
  );
};

const PlusMinusToggle = ({ isOpen }) => {
  return (
    <div className="mt-2 cursor-pointer">
      <svg
        width="24"
        height="24"
        viewBox="0 0 24 24"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        style={{
          transition: "all 0.3s ease-in-out",
          transform: isOpen ? "rotate(0deg)" : "rotate(90deg)",
        }}
      >
        <rect
          x="11"
          y="4"
          width="2"
          height="16"
          fill={isOpen ? "transparent" : "var(--washedGreen)"}
          style={{
            transition: "all 0.3s ease-in-out",
          }}
        />
        <rect
          x="4"
          y="11"
          width="16"
          height="2"
          fill={isOpen ? "white" : "var(--washedGreen)"}
        />
      </svg>
    </div>
  );
};

export default News;
