import { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  ChevronDown,
  Mail,
  Menu,
  Code,
  Palette,
  Camera,
  Music,
} from "lucide-react";
import gsap from "gsap";
import MouseFollower from "mouse-follower";

// SECTIONS
import { Hero } from "./sections/Hero";
import { About } from "./sections/About";
import { Gallery } from "./sections/Gallery";
import News from "./sections/News";
import { Contact } from "./sections/Contact";

// SCRIPTS
import Section from "./scripts/Section.js";
import Star4Sides from "./scripts/Star4Sides.js";
import DiagonalDivider from "./scripts/DiagonalDivider.js";
import useBackgroundAnimation from "./scripts/UseBackgroundAnimation.js";
import RevealText from "./scripts/RevealText";
import RevealTextAlt from "./scripts/RevealTextAlt";
import Text from "./scripts/Text";
import BurgerIcon from "./scripts/BurgerIcon";
import CustomMarquee from "./scripts/CustomMarquee";

// ASSETS
import bgStripes from "./assets/svg/bgPattern.svg";
import grunge from "./assets/png/grungy.png";
import emblem from "./assets/svg/emblem.svg";

// GRID PATTERNS
import bgGrid from "./assets/svg/grid.svg";
import bgGrid2 from "./assets/svg/grid2.svg";
import bgGridCross from "./assets/svg/grid_cross.svg";
import bgGridCross2 from "./assets/svg/grid_cross2.svg";
import bgGridHash from "./assets/svg/grid_hash.svg";
import bgTileableStripe from "./assets/svg/tileable_stripe.svg";

// STRIPED PATTERNS
import stripedPattern from "./assets/svg/pattern.svg";
import stripes from "./assets/svg/stripe.svg";
import stripedPatternLineW from "./assets/svg/pattern_linew.svg";
import stripedPatternThick from "./assets/svg/pattern2.svg";

// CARS
import dacia1300 from "./assets/png/cars/dacia1300.png";
import ferrariF40 from "./assets/png/cars/ferrarif40.png";
import gtrR32 from "./assets/png/cars/gtrr32.png";
import peugeot205 from "./assets/png/cars/peugeot205.png";
import peugeot206 from "./assets/png/cars/peugeot206.png";
import trueno from "./assets/png/cars/trueno.png";
import { tr, use } from "framer-motion/client";
import { data } from "autoprefixer";

export default function Component() {
  const [selectedarticle, setSelectedarticle] = useState(null);
  const [menuOpen, setMenuOpen] = useState(false);
  const [animationsEnabled, setAnimationsEnabled] = useState(true);
  const [showMotto, setShowMotto] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [resetAnimation, setResetAnimation] = useState(false);
  const { bgX, bgY } = useBackgroundAnimation(
    animationsEnabled,
    1,
    1,
    1,
    1,
    25,
    1024
  );
  const { bgX: bgStripeX, bgY: bgStripeY } = useBackgroundAnimation(
    animationsEnabled,
    -0.75,
    0.75,
    0.25,
    0.25,
    25,
    1024
  );
  const scrollYRef = useRef(0);
  const tickingRef = useRef(false);

  const headerHeight = document.querySelector("header")?.offsetHeight || 0;

  //MouseFollower
  useEffect(() => {
    if (window.matchMedia("(min-width: 768px)").matches) {
      MouseFollower.registerGSAP(gsap);
      const mouseFollower = new MouseFollower({
        skewing: 6,
        stickDelta: 0.009,
        speed: 0.2,
        stateDetection: {
          "-hidden": ".swiper-slide",
        },
      });
      return () => {
        mouseFollower.destroy();
      };
    }
  }, []);

  // Window Resize Listener
  function useWindowSize() {
    const [windowSize, setWindowSize] = useState({
      w: window.innerWidth,
      h: window.innerHeight,
    });
    useEffect(() => {
      function handleResize() {
        setWindowSize({
          w: window.innerWidth,
          h: window.innerHeight,
        });
      }
      window.addEventListener("resize", handleResize);
      handleResize();
      return () => window.removeEventListener("resize", handleResize);
    }, []);
    return windowSize;
  }

  // Scroll Indicator
  const handleScroll = () => {
    scrollYRef.current = window.scrollY;
    if (!tickingRef.current) {
      window.requestAnimationFrame(() => {
        setScrollY(scrollYRef.current);
        tickingRef.current = false;
      });
      tickingRef.current = true;
    }
  };

  const scrollIndicatorStyle = {
    top: `${
      (scrollY / (document.body.scrollHeight - window.innerHeight)) * 0.93 * 100
    }%`,
  };

  useEffect(() => {
    const heroSection = document.getElementById("hero");
    const handleScrollVisibility = () => {
      if (heroSection) {
        const { top, bottom } = heroSection.getBoundingClientRect();
        const headerHeight =
          document.querySelector("header")?.offsetHeight || 0;

        if (top <= headerHeight && bottom > 0) {
          setShowMotto(false);
        } else {
          setShowMotto(true);
        }
      }
      handleScroll();
    };

    window.addEventListener("scroll", handleScrollVisibility);
    handleScrollVisibility();

    return () => window.removeEventListener("scroll", handleScrollVisibility);
  }, []);

  useEffect(() => {
    if (scrollY > 0) {
      setResetAnimation(true);
    } else setResetAnimation(false);
  }, [scrollY]);

  const sections = ["Hero", "About", "Gallery", "News", "Contact"];

  return (
    <div className="relative w-screen bg-darkRotaryGreen text-white font-playfair tracking-normal z-0">
      <motion.div
        className="absolute inset-0 z-[-1] bg-black"
        initial={{ opacity: 1 }}
        animate={{ opacity: 0 }}
        exit={{ opacity: 0 }}
        transition={{ duration: 1 }}
      />

      {/* Custom Scrollbar */}
      <div className="hidden lg:block fixed my-1 lg:my-2 right-1 lg:right-2 h-full w-1 lg:w-2 mix-blend-exclusion bg-transparent z-50">
        <div
          className="w-full bg-white h-[5%]"
          style={{ position: "absolute", ...scrollIndicatorStyle }}
        ></div>
      </div>
      <meta
        name="viewport"
        content="width=device-width, initial-scale=1.0"
      ></meta>

      {/* Menu */}
      <AnimatePresence>
        {menuOpen && (
          <motion.div
            className="fixed inset-0 bg-red-900 z-50"
            initial={{ y: "-100%" }}
            animate={{ y: 0 }}
            exit={{ y: "-100%" }}
            transition={{ ease: "circInOut", duration: 0.5 }}
          >
            <motion.div
              className="absolute inset-0 z-[-1] bg-[length:512] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-100"
              style={{
                backgroundImage: `url(${bgGridCross2}), url(${bgGridHash})`,
                mixBlendMode: "soft-light",
                backgroundPositionX: bgX,
                backgroundPositionY: bgY,
              }}
            />

            {/* Prompt icon */}
            <div
              className={`z-50 fixed top-0 left-0 z-10 mt-3 lg:mt-12 mx-8 lg:mx-12
            ${menuOpen ? "lg:mix-blend-normal" : "lg:mix-blend-exclusion"}`}
            >
              <button
                onClick={() => setAnimationsEnabled(!animationsEnabled)}
                className="font-justSansBold text-xl lg:text-3xl px-2 py-1 lg:px-4 lg:py-2 bg-black text-white border-2 border-black lg:hover:border-white lg:hover:bg-red-900 transition-colors duration-300"
              >
                {animationsEnabled ? "ANIMATED" : "STATIC"}
              </button>
            </div>

            <div className="h-svh space-8 container mx-auto flex items-center justify-center">
              <div className="flex flex-col gap-3 lg:gap-6 px-6 text-offWhite text-5xl lg:text-8xl font-justSansBold font-bold tracking-tight">
                {sections.map((item, index) => (
                  <motion.a
                    href={`#${item.toLowerCase()}`}
                    onClick={(e) => {
                      e.preventDefault();
                      setMenuOpen(!menuOpen);
                      const targetElement = document.getElementById(
                        item.toLowerCase()
                      );
                      if (targetElement) {
                        const getOffsetTop = (el) => {
                          let offsetTop = 0;
                          while (el) {
                            offsetTop += el.offsetTop;
                            el = el.offsetParent;
                          }
                          return offsetTop;
                        };
                        const offsetValue =
                          window.innerWidth > window.innerHeight
                            ? 0
                            : headerHeight;
                        const offsetPosition =
                          getOffsetTop(targetElement) - offsetValue;
                        window.scrollTo({
                          top: offsetPosition,
                          behavior: "smooth",
                        });
                      }
                    }}
                    key={item}
                    data-shadow={`${item.toUpperCase()} ///`}
                    className="w-fit animShadow hover:text-red-900 [-webkit-text-stroke-width:1px] lg:[-webkit-text-stroke-width:2px] [-webkit-text-stroke-color:var(--offWhite)]"
                    initial={{ opacity: 0, x: -1000 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{
                      delay: 0.2 + index * 0.1,
                      duration: 0.5,
                      ease: "circInOut",
                    }}
                  >
                    {item.toUpperCase()} ///
                  </motion.a>
                ))}
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Header */}
      <AnimatePresence>
        {!menuOpen && (
          <motion.header
            id="header"
            className="block lg:hidden bg-black fixed top-0 left-0 right-0 z-50"
            initial={{ y: "-100%" }}
            animate={{ y: 0 }}
            exit={{ y: "-100%" }}
            transition={{ ease: "circInOut", duration: 0.5 }}
          >
            <nav className="mx-8 flex justify-between items-center py-4">
              <div
                onClick={() => window.scrollTo({ top: 0, behavior: "smooth" })}
                className="flex flex-row space-x-1 lg:space-x-2 items-center justify-center"
              >
                <Star4Sides className="w-auto h-4 lg:h-6 stroke-2 stroke-white fill-transparent" />
                <Star4Sides className="w-auto h-4 lg:h-6 stroke-2 stroke-white fill-white" />
                <h1 className="text-white text-1xl lg:text-2xl font-justSansBold font-bold tracking-tighter">
                  {Text.hero.title}
                </h1>
              </div>
            </nav>
          </motion.header>
        )}
      </AnimatePresence>

      {/* Burger icon */}
      <motion.div
        initial={{ opacity: "0%", y: "-100%" }}
        animate={{ opacity: "100%", y: 0 }}
        exit={{ opacity: "0%", y: "-100%" }}
        transition={{ ease: "circInOut", duration: 0.5 }}
        className={`z-50 fixed top-0 right-0 z-10 mt-3 lg:mt-12 mx-8 lg:mx-12
        ${menuOpen ? "lg:mix-blend-normal" : "lg:mix-blend-exclusion"}`}
      >
        <button
          onClick={() => {
            setMenuOpen(!menuOpen);
          }}
        >
          <BurgerIcon menuOpen={menuOpen} />
        </button>
      </motion.div>

      <main>
        {/* Hero Section */}
        <Hero
          bgTileableStripe={bgTileableStripe}
          grunge={grunge}
          bgGrid={bgGrid}
          scrollY={scrollY}
          showMotto={showMotto}
          bgStripeX={bgStripeX}
          bgStripeY={bgStripeY}
          bgX={bgX}
          bgY={bgY}
          useWindowSize={useWindowSize}
        />

        {/* About Section */}
        <About
          bgGridCross2={bgGridCross2}
          sections={sections}
          bgX={bgX}
          bgY={bgY}
        />

        {/* Gallery Section */}
        <Gallery
          bgGrid2={bgGrid2}
          bgGridHash={bgGridHash}
          sections={sections}
          bgX={bgX}
          bgY={bgY}
        />

        {/* News Section */}
        <News
          bgGridCross2={bgGridCross2}
          bgGrid2={bgGrid2}
          setSelectedarticle={setSelectedarticle}
          bgX={bgX}
          bgY={bgY}
          sections={sections}
          headerHeight={headerHeight}
        />

        {/* Contact Section */}
        <Contact bgGrid2={bgGrid2} bgX={bgX} bgY={bgY} sections={sections} />
      </main>

      <footer className="relative bg-neutral-900 text-white py-4">
        <div className="relative font-justSans text-sm container mx-auto px-4 text-center">
          <p className="">
            &copy; 2024{" "}
            <img
              src={emblem}
              alt="emblem"
              className="h-[1em] inline-block mb-0.5"
            />{" "}
            Cotorceanu Radu | All Rights Reserved
          </p>
        </div>
      </footer>
    </div>
  );
}
