import { motion, MotionConfig } from "framer-motion";

const MySvgComponent = ({ menuOpen }) => {
    const VARIANTS = {
        top: {
            open: {
                rotate: ["0deg", "0deg", "45deg"],
                top: ["20%", "50%", "50%"],
            },
            closed: {
                rotate: ["45deg", "0deg", "0deg"],
                top: ["50%", "50%", "20%"],
            },
        },
        middle: {
            open: {
                rotate: ["0deg", "0deg", "-45deg"],
            },
            closed: {
                rotate: ["-45deg", "0deg", "0deg"],
            },
        },
        bottom: {
            open: {
                rotate: ["0deg", "0deg", "45deg"],
                bottom: ["20%", "50%", "50%"],
            },
            closed: {
                rotate: ["45deg", "0deg", "0deg"],
                bottom: ["50%", "50%", "20%"],
            },
        },
    };

    return (
        <MotionConfig
            transition={{
                duration: 0.3,
                ease: "easeOut",
            }}
        >
            <motion.div
                initial={false}
                animate={menuOpen ? "open" : "closed"}
                className="relative h-8 lg:h-12 w-8 lg:w-12"
            >
                <motion.span
                    variants={VARIANTS.top}
                    className="absolute h-2 w-full bg-white"
                    style={{ y: "-50%", left: "50%", x: "-50%", top: "20%" }}
                />
                <motion.span
                    variants={VARIANTS.middle}
                    className="absolute h-2 w-full bg-white"
                    style={{ left: "50%", x: "-50%", top: "50%", y: "-50%" }}
                />
                <motion.span
                    variants={VARIANTS.bottom}
                    className="absolute h-2 w-full bg-white"
                    style={{ x: "-50%", y: "50%", bottom: "20%", left: "50%", }}
                />
            </motion.div>
        </MotionConfig>
    );
};

export default MySvgComponent;
