import React from "react";
import { motion } from "framer-motion";
import Section from "../scripts/Section";
import CustomMarquee from "../scripts/CustomMarquee";
import Carousel from "../scripts/Carousel";

export function Gallery({ bgGrid2, bgGridHash, sections, bgX, bgY }) {
  return (
    <div className="overflow-hidden relative bg-black">
      <motion.div
        className="absolute inset-0 bg-[length:512] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-60"
        style={{
          backgroundImage: `url(${bgGridHash})`,
          mixBlendMode: "normal",
          backgroundPositionX: bgX,
          backgroundPositionY: bgY,
        }}
      />
      <motion.div
        className="absolute inset-0 bg-[length:512] lg:bg-[length:1024px] bg-repeat bg-fixed opacity-20"
        style={{
          backgroundImage: `url(${bgGrid2})`,
          mixBlendMode: "normal",
          backgroundPositionX: bgX,
          backgroundPositionY: bgY,
        }}
      />
      <Section id="gallery" className="relative">
        <CustomMarquee
          name={sections.at(2)}
          direction={
            sections.indexOf(sections.at(2)) % 2 === 0 ? "right" : "left"
          }
        />

        <Carousel className="mx-auto py-16 lg:py-32" />
      </Section>
    </div>
  );
}
